import { IAction } from '../action';

export const SELECT_NAVIGATION_TAB = 'SELECT_NAVIGATION_TAB';
export type SELECT_NAVIGATION_TAB = number;

export const SHOW_ERROR = 'SHOW_ERROR';
export type SHOW_ERROR = string;

export const UPDATE_ROUTE = 'UPDATE_ROUTE';
export type UPDATE_ROUTE = string;

export const navigationActions = {
    SELECT_NAVIGATION_TAB:
    (tabId: SELECT_NAVIGATION_TAB) =>
        <IAction<SELECT_NAVIGATION_TAB>>
        { type: SELECT_NAVIGATION_TAB, data: tabId },

    SHOW_ERROR:
    (message: SHOW_ERROR) =>
        <IAction<SHOW_ERROR>>
        { type: SHOW_ERROR, data: message },

    UPDATE_ROUTE:
    (route: UPDATE_ROUTE) =>
        <IAction<UPDATE_ROUTE>>
        { type: UPDATE_ROUTE, data: route }
}


